body {
  font-family: 'K2D', sans-serif;
  margin: 0;
  padding: 0;
}

header {
  align-items: center;
  display: flex;
  left: 50%;
  max-width: 1120px;
  padding: 30px 0;
  position: absolute;
  top: 0;
  transform: translateX(-50%);
  width: 90%;
}

nav {
  display: flex;
  font-size: 36px;
  line-height: 40px;
}

nav a {
  display: block;
  margin-left: 20px;
}

section {
  min-height: 600px;
  padding: 200px 0;
}

.hero {
  background: #42D8ED;
  text-align: center;
}

article {
  margin: 0 auto;
  max-width: 820px;
  width: 90%;
}

a {
  color: black;
}

h1 {
  flex-grow: 1;
  font-size: 30px;
  font-weight: 700;
  line-height: 40px;
  margin: 0;
  padding: 0;
}

h2 {
  flex-grow: 1;
  font-size: 40px;
  font-weight: 800;
  line-height: 60px;
  margin: 0 0 40px;
  padding: 0;
}

p {
  font-size: 20px;
  font-weight: 400;
  line-height: 36px;
  margin: 0 0 40px 0;
}

.button {
  align-items: center;
  background: #E52A95;
  border-radius: 4px;
  color: white;
  display: inline-flex;
  font-size: 30px;
  padding: 8px 16px;
  text-decoration: none;
  text-transform: uppercase;
}

.button span {
  font-size: 16px;
  margin-left: 8px;
}

.code-window {
  background-color: white;
  border-radius: 4px;
  box-shadow: 0 8px 24px 0 rgba(0, 0, 0, 0.1);
  margin: 60px 0 0 0;
  min-height: 400px;
  padding: 20px;
}

.code-window .dots {
  align-items: center;
  display: flex;
}

.code-window .dots div {
  background-color: #e9ecef;
  border-radius: 50%;
  height: 12px;
  margin-right: 8px;
  width: 12px;
}

.code-window .dots div:nth-of-type(1) {
  background: #ff1744;
}

.code-window .dots div:nth-of-type(2) {
  background: #f6c343;
}

.code-window .dots div:nth-of-type(3) {
  background: #5cc72a;
}